}


/******************************************************************
PAGE NAVI STYLES
******************************************************************/

.pagination,
.wp-prev-next {
	margin: 1.5em 0;
}
	.pagination{
    text-align: center;

    ul {
      display: inline-block;
      background-color:white;
      white-space: nowrap;
      padding: 0;
      clear: both;
      border-radius: 3px;
    }

    li {
      padding: 0;
      margin: 0;
      float: left;
      display: inline;
      overflow: hidden;
      &:last-child{
      	border: none;
      }
    }

    a, span {
      margin: 0;
      text-decoration: none;
      padding: 0;
      line-height: 1em;
      font-size: 1em;
      font-weight: normal;
      padding: 0.75em;
      min-width: 1em;
      display: block;

      &:hover, &:focus {
      	color:white;
      }
    }

    .current{
    	cursor: default;


    	&:hover, &:focus {
    		background-color:white;

    	}
    }

  } /* end .bones_page_navi */

	/* fallback previous & next links */
	.wp-prev-next {

		ul {}

		.prev-link {
			float: left;
		}

		.next-link {
			float: right;
		}

	} /* end .wp-prev-next */


/*********************
LISTE 
*********************/

.entry-content{
	ul{
		list-style: disc;
		li{
			margin-left: 20px;
		}
	}
}




/*********************
FOOTER STYLES
*********************/

.footer {
	clear: both;
	background-color:white;
	color: var(--black);
	padding: 40px 0 20px;
}

.footer-links.nav{
	display: flex;
	justify-content: flex-end;
	margin: 0;
	top: 0;
		&:after, &:before{
		 display: none;  // remove from flex alignment! 
	}
		li {
			flex-basis: auto;
			margin: 0 10px;
			padding: 0;
			height: auto;
			font-size: 1.2em;


			&>a, &>a:hover {
				text-decoration: none;
				margin: 0;
				padding: 0;
				font-size: 1em;
			}
			ul {}
		}

} /* end .footer-links */


	.copyright {
		margin: 0;
		float: left;
		font-size: 12px;
			a{
		text-decoration: none;
	}
}
